:root {
	--showTabbar: flex;
	--padding: calc(var(--safe-area-inset-bottom) + var(--tabbar_height));
}
.Tabbar {
	display: var(--showTabbar);
}
.Panel__in {
	padding-bottom: var(--padding)!important;
}
.Gallery__bullets {
	bottom: calc(var(--safe-area-inset-bottom) + 10px);
}
.Div, .Header {
	-webkit-user-select: none;
	user-select: none;
}
.Epic .FixedLayout--bottom {
	padding-bottom: calc(var(--padding) + var(--safe-area-inset-bottom));
}
@media screen and (max-width: 333px) {
	.IntroImg {
		width: 250px!important;
		height: 250px!important;
	}
	.Button--sz-l .Button__content {
		font-size: 13px;
	}
}
@media screen and (max-width: 313px) {
	.Button--sz-l .Button__content {
		font-size: 11px;
	}
}
@media screen and (max-width: 298px) {
	.Button--sz-l .Button__content {
		font-size: 9px;
	}
}
@media screen and (max-width: 283px) {
	.Button--sz-l .Button__content {
		font-size: 8.2px;
	}
}